<template>
    <div>
        <div id="top">
        <el-select class="mainSelect" :placeholder="datas" v-model="value">
            <el-option
                v-for="(item,index) in options"
                :key="index"
                :label="item.label"
                :value="item.value"
                :path="item.path">
            </el-option>
        </el-select>
        </div>
        <div id="left">
            <p class="news-title">
                <button><img src="../assets/news-pic.png" alt="" width="28px" style="vertical-align: bottom;">疫情最新新闻/News List</button>
            </p>
            <div class="news">
                <div class="data" v-for="item in newsData" :key="item.url">
                    <p class="time"><img src="../assets/time.png" width="10px">{{item.realPublishTime}}</p>
                    <p class="title"><img src="../assets/logo4.png" width="20px"><a :href="item.url" target="_blank">{{item.title}}</a></p>
                    <p class="content">{{item.abs.substring(3, item.abs.indexOf(p))}}</p>
                    <p class="origin"><img src="../assets/logo2.png" width='20px'>来源：{{item.authorName}}</p>
                    <el-divider></el-divider>
                </div>
            </div>
        </div>
        <div id="right">
            <div class="data-title">
                <button><img src="../assets/yq-data.png" alt="" width="28px" style="vertical-align: bottom;"><span>疫情数据/Data List</span></button>
            </div>
            <div class="data-all">
                <div class="data-head">
                    <p class="data-name">地区</p>
                    <p class="data-current">现存</p>
                    <p class="data-total">累计</p>
                    <p class="data-input">输入</p>
                </div>
                <div class="datalist">
                    <p class="datalist-name">中华人民共和国</p>
                     <el-divider></el-divider>
                    <div class="china-data">
                        <div class="up">
                            <div>
                                <p style="font-size: 13px;color:#333">较昨日<span class="inc-ljqz" style="color: #fc6565"></span></p>
                                <p style="color: #fc6565;font-weight: 1000;font-size: 17px;" class="ljqz"></p>
                                <p style="font-size:13px;color:#222">累计确诊</p>
                            </div>
                            <div>
                                <p style="font-size: 13px;color:#333">较昨日<span class="inc-ljsw" style="color: #9e9e9e"></span></p>
                                <p style="color: #9e9e9e;font-weight: 1000;font-size: 17px;" class="ljsw"></p>
                                <p style="font-size:13px;color:#222">累计死亡</p>
                            </div>
                        </div>
                        <div class="down">
                            <div>
                                <p style="font-size: 13px;color:#333">较昨日<span class="inc-xcqz" style="color: #81c6cc"></span></p>
                                <p style="color: #81c6cc;font-weight: 1000;font-size: 17px;" class="xcqz"></p>
                                <p style="font-size:13px;color:#222">现存确诊</p>
                            </div>
                            <div>
                                <p style="font-size: 13px;color:#333">较昨日<span class="inc-jwsr" style="color: orange"></span></p>
                                <p style="color: orange;font-weight: 1000;font-size: 17px;" class="jwsr"></p>
                                <p style="font-size:13px;color:#222">境外输入</p>
                            </div>
                        </div>
                    </div>
                    <el-divider></el-divider>
                    <div class="dataChild" v-if="datas=='全球疫情数据'">
                        <el-collapse accordion>
                            <el-collapse-item  v-for="(currentData,index) in currentData.continentDataList" :key="index" :title="currentData.continentName">
                                <div class="data-body" v-for="countriesData in currentData.countriesData" :key="countriesData.childStatistic">
                                    <p class="data-name">{{countriesData.childStatistic}}</p>
                                    <p class="data-current">{{countriesData.currentConfirm}}</p>
                                    <p class="data-total">{{countriesData.totalConfirmed}}</p>
                                    <p class="data-death">{{countriesData.totalDeath}}</p>
                                    <el-divider></el-divider>
                                </div>
                            </el-collapse-item>
                        </el-collapse>
                    </div>
                    <div class="dataChild" v-if="datas=='国内疫情数据'">
                        <div class="data-body" v-for="(currentData,index) in currentData.provinceArray" :key="currentData.childStatistic">
                            <p class="data-name">{{currentData.childStatistic}}</p>
                            <p class="data-current">{{currentData.currentConfirm}}</p>
                            <p class="data-total">{{currentData.totalConfirmed}}</p>
                            <p class="data-input">{{jwsr[index]}}</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name:'slideData',
     props:{
        datas: String,
        res:Object,
        required: true
    },
    data(){
        return{
            options:[{
                value:'选项1',
                label:'全球疫情数据',
                path:'/pages/intIncTrend'
            },{
                value:'选项2',
                label:'国内疫情数据',
                path:'/pages/fluData'
            }],
            newsData:[],
            currentData:{},
            jwsr:[],
            value:'',
            p:'</p>'
        }
    },
    created(){
        $.getScript("https://cdn.mdeer.com/data/yqstaticdata.js?callback=callbackstaticdata&t=" + (+new Date));
        $.getScript("https://cdn.mdeer.com/contentdtos.js?callback=callbackcontentdtos&t=" + (+new Date));
    },
    watch:{
        res(){
            var that =this;
            that.currentData = this.$props.res;
            // 右侧数据初始化
            $('.ljqz').html(that.currentData.country.totalConfirmed);
            $('.xcqz').html(that.currentData.country.currentConfirm);
            $('.ljsw').html(that.currentData.country.totalDeath);
            $('.jwsr').html(that.currentData.country.abroadInputConfirmed);
            $('.inc-ljqz').html(that.currentData.incCountry.incConfirmedFlag + that.currentData.incCountry.incConfirmed);
            $('.inc-xcqz').html(that.currentData.incCountry.incCurConfirmedFlag + that.currentData.incCountry.incCurConfirmed);
            $('.inc-ljsw').html(that.currentData.incCountry.incDeathFlag + that.currentData.incCountry.incDeath);
            $('.inc-jwsr').html(that.currentData.incCountry.incAbroadInputConfirmedFlag + that.currentData.incCountry.incAbroadInputConfirmed);
            for (let i = 0; i < that.currentData.provinceArray.length; i++) {
                    if (that.currentData.provinceArray[i].cityArray[0].childStatistic == '境外输入') {
                        that.jwsr.push(that.currentData.provinceArray[i].cityArray[0].currentConfirm);
                    }else{
                        that.jwsr.push(0);
                    }
            }
        }
    },
    mounted(){
        var that = this;
        //兼容
        var h1 = document.getElementsByClassName('data-head')[0].clientHeight;
        var h2 = document.getElementsByClassName('data-title')[0].clientHeight;
        var rightHeight = document.getElementById('right').clientHeight;
        document.getElementsByClassName('datalist')[0].style.height = rightHeight - h1 - h2 -28 + 'px';
        document.getElementsByClassName('datalist')[0].style.display = 'block';
        var h3 = document.getElementsByClassName('news-title')[0].clientHeight;
        var leftHeight = document.getElementById('left').clientHeight;
        document.getElementsByClassName('news')[0].style.height = leftHeight - h3 - 33 + 'px';
        document.getElementsByClassName('news')[0].style.display = 'block';

        //实时数据接口  
        if(this.$props.msg!='fluData'){
            window.callbackstaticdata = function(respone) {
            that.$emit('sendData',respone);
            console.log(1111);
            that.currentData = respone;
            // 右侧数据初始化
            $('.ljqz').html(that.currentData.country.totalConfirmed);
            $('.xcqz').html(that.currentData.country.currentConfirm);
            $('.ljsw').html(that.currentData.country.totalDeath);
            $('.jwsr').html(that.currentData.country.abroadInputConfirmed);
            $('.inc-ljqz').html(that.currentData.incCountry.incConfirmedFlag + that.currentData.incCountry.incConfirmed);
            $('.inc-xcqz').html(that.currentData.incCountry.incCurConfirmedFlag + that.currentData.incCountry.incCurConfirmed);
            $('.inc-ljsw').html(that.currentData.incCountry.incDeathFlag + that.currentData.incCountry.incDeath);
            $('.inc-jwsr').html(that.currentData.incCountry.incAbroadInputConfirmedFlag + that.currentData.incCountry.incAbroadInputConfirmed);
            for (let i = 0; i < that.currentData.provinceArray.length; i++) {
                    if (that.currentData.provinceArray[i].cityArray[0].childStatistic == '境外输入') {
                        that.jwsr.push(that.currentData.provinceArray[i].cityArray[0].currentConfirm);
                    }else{
                        that.jwsr.push(0);
                    }
            }
        }
        }

        //新闻接口
        window.callbackcontentdtos = function(res) {
            that.newsData = res;
        }

        $('#top .el-select-dropdown__item').on('click',function(){
            that.$router.push($(this).attr('path'));
        })
    }
}
</script>


<style scoped>
.mainSelect{
    position: fixed;
    top: 30px;
    left: 50%;
    z-index: 999;
    transform:translateX(-50%);
}
/* /deep/.el-input__inner{
    border: 2px solid rgb(0, 0, 0) !important;
    border-radius: 15px !important;
} */
p{
    color:#666;
}
.data-body p:nth-of-type(1){
    color: #666;
}
.data-head p:nth-of-type(1){
    color: #666;
}
#left {
      height: 90%;
      width: 20%;
      background:rgb(49, 108, 218);
      position: absolute;
      top: 4%;
      margin: 10px;
      z-index: 99;
      color: black;
      font-size: 13px;
      text-align: left;
      border-radius: 5px;
  }
  
   ::-webkit-scrollbar {
      display: none;
  }
  
  .datalist,
  .news {
      scrollbar-width: none;
      -ms-overflow-style: none;
  }
  
  .el-divider--horizontal{
      margin: 10px;
      width: 90%;
      margin-left: 5%;
  }
  .data {
      padding: 2px;
      width: 90%;
      margin-left: 4.5%;
       background:rgb(255, 255, 255);
  }
  .data:nth-of-type(1){
      margin-top: 15px;
  }
  .title a {
      font-size: 15px;
      font-weight: 1000;
      color: rgb(110, 110, 228);
      border-bottom: 1px solid rgb(110, 110, 228);
      text-decoration: none;
      cursor: pointer;
  }
  
  #left p {
      width: 100%;
  }
  .news-title {
      position: absolute;
      color: orange;
      font-size: 14px;
      font-weight: 1000;
      text-align: center;
      margin: 0;
      left: 48%;
      transform: translateX(-50%);
      top: 10px;
  }
  
  .title a:hover {
      color: rgb(126, 200, 250);
      border-color: rgb(126, 200, 250);
  }
  
  .data-title img {
      margin-right: 5px;
  }
  
  .news-title button {
      background: none;
      border: none;
      outline: none;
      color: rgb(255, 255, 255);
      height: 45px;
      padding: 0;
      font-size: 21px;
      font-family: font2;
  }
  .news-title img{
      margin-right: 5px;
  }
  .origin {
      color: orange;
      font-weight: bolder;
  }
  
  .origin img {
      vertical-align: top;
  }
  
  .time img {
      width: 18px;
      vertical-align: bottom;
      margin-right: 5px;
  }
  
  .title img {
      vertical-align: top;
      margin-right: 5px;
  }
  
  .news {
      position: relative;
      height: 86%;
      top: 100%;
      transform: translateY(-102%);
      overflow-y: scroll;
      background: white;
      width: 92%;
      left: 4%;
  }
  .data-body div{
    float: left;
}
  #right {
      height: 80%;
      width: 18%;
      position: absolute;
      right: 10px;
      top: 10%;
      z-index: 99;
      background:rgb(49, 108, 218);
      border-radius: 5px;
  }
  .datalist .el-divider--horizontal{
      margin: 0;
      margin-left: 5%;
  }
  table {
      margin: auto;
      text-align: center;
  }

  .data-title {
      margin-top: 10px;
  }
  
  .data-title button {
      border: none;
      width: 230px;
      height: 40px;
      position: relative;
      color: white;
      outline: none;
      font-size: 21px;
      font-family: font2;
      text-align: center;
      background: none;
      margin-bottom: 10px;
  }
  
  .data-head,
  .data-body {
      overflow: hidden;
      width: 94%;
      margin: 0;
      margin-left: 3%;
      background:#fff;
       
  }
  
  .data-head p,
  .data-body p{
      margin: 0;
      width: 24%;
      float: left;
      padding: 0;
      margin-left: 1%;
      margin-top: 5px;
      margin-bottom: 5px;
      font-size: 14px;
      font-weight: 1000;
      text-align: center;
  }
  .dataChild p{
      font-weight: normal;
  }
  .data-body {
     padding-top: 2px;
     padding-bottom: 2px;
  }
  .data-head{
      padding-top: 5px;
      padding-bottom: 5px;
  }
  .data-body p {
      font-size: 13px;
  }
  
  .data-all {
      height: 100%;
  }
  
  .datalist {
      overflow-y: scroll;
      position: relative;
      height: 80%;
      top: 100%;
      margin-top: -112px;
      transform: translateY(-100%);
      background: white;
      width: 94%;
      left: 3%;
  }
  
  .data-name {
      color: #eee;
  }
  
  .data-current {
      color: tomato;
  }
  
  .data-total {
      color: orange;
  }
  
  .data-input {
      color: rgb(98, 98, 255);
  }
  
  .china-data {
      width: 90%;
      margin-left: 5%;
      overflow: hidden;
  }
  
  .up,
  .down {
      width: 48%;
      margin-left: 1.5%;
      float: left;
  }
  
  .up p,
  .down p {
      margin: 6px;
      text-align: center;
  }
  
  .up div p:nth-of-type(1) {
      padding-top: 10px;
  }
  
  .up div p:nth-of-type(3) {
      padding-bottom: 10px;
  }
  
  .down div p:nth-of-type(1) {
      padding-top: 10px;
  }
  
  .down div p:nth-of-type(3) {
      padding-bottom: 10px;
  }
  
  .up div:nth-of-type(1) {
      background-size: 100% 100%;   
  }
  
  .up div:nth-of-type(2) {
      background-size: 100% 100%;
        margin-top: 5px;
  }
  
  .down div:nth-of-type(1) {
      background-size: 100% 100%;
  }
  
  .down div:nth-of-type(2) {
      background-size: 100% 100%;
      margin-top: 5px;
  }
  
  .datalist-name {
      font-weight: 1000;
      text-align: center;
      margin: 10px;
      margin-top: 4px;
      margin-bottom: 5px;
      padding-top: 10px;
      padding-bottom: 10px;
      border-top-left-radius: 10px;
      border-bottom-right-radius: 10px;
  }
  .data-body p {
      font-size: 12px;
  }
  /deep/ .el-collapse-item__header{
      width:85%;
      height: 45px;
      margin-left: 5% !important;
      color:#666;
      font-size: 13px;
      padding-left: 5%;
      margin-left:2px;
      border-radius: 5px;
      border:none;
      margin-top:7px;
      font-weight: 1000;
      background: rgb(247, 247, 247);
  }
  /deep/ .el-collapse{
      border:none;
  }
  /deep/ .el-collapse-item__wrap{
      background: none;
      border: none;
  }
</style>


