<template>
    <div class="nav animated fadeInDown">
        <section  class="to" path="/prevent" index='1'>
            <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-yufangbaojianke"></use>
            </svg>
            <p>防疫篇</p>
        </section>
        <section  class="to" path="/deeds" index='2'>
            <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-renyuan"></use>
            </svg>
            <p>事迹篇</p>
        </section>
        <section  class="to" path="/fluData" index='3'>
            <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-icon_huabanfuben"></use>
            </svg>
            <p>数据篇</p>
        </section>
        <section class="to" path="/wish" index='4'>
            <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-aixin"></use>
            </svg>
            <p>祝福篇</p>
        </section>
    </div>
</template>

<script>
export default {
    name:'NavMenu',
    mounted(){
        var that = this;
        var flag = true;
        var nav =  document.querySelectorAll('.to');
        for(let i=0;i<nav.length;i++){
            nav[i].addEventListener('click',function(e){
                $('.push').removeClass('rotate');
                $('.push').css('top','0px');
                $('.gz').css('height','50px');
                if(flag){
                    console.log(flag);
                    setTimeout(() => {
                    that.$notify({
                    title: '温馨提示',
                    message: '您可以点击上方星星来获取导航栏',
                    offset: 160,
                    type:'info',
                    duration:2500,
                    });
                }, 1000);
                flag = !flag;
                }
                if(document.querySelectorAll('.nav-mask')[0]){
                    document.querySelectorAll('.nav-mask')[0].style.display = 'none';
                }
                var path =e.currentTarget.getAttribute('path');
                that.$emit('show','子组件修改了');
                that.$router.push('/pages'+path);
            })
        }
    }
}
</script>

<style scoped>
.nav{
    position: fixed;
    display: flex;
    flex-direction: row;
    width: 40%;
    margin-left: 30%;
    justify-content: space-around;
    background-image: linear-gradient(to right,rgb(247, 168, 21),rgb(255, 108, 50));
    top: 35%;
    padding: 20px;
    border-radius: 5px;
}
.nav section p{
    margin: 0;
    margin-top: 10px;
    font-size: 18px;
    font-weight: 500;
    color: white;
    transition: all 0.3s ease-in;   
    cursor: pointer;
    font-family: font2;
}
.nav section{
    padding: 25px;
    transition: all 0.3s ease-in;  
    cursor: pointer;
    border:2px solid rgba(0,0,0,0);
}
.nav section:hover{
    background:white;
    border-radius: 1rem;
    border:2px solid lightpink;
    box-shadow: 0 0 2px 2px white;
}
.nav section:hover  p{
    /* color: tomato; */
    animation: text-blur 0.3s;
    animation-fill-mode: forwards;
}
.nav section:hover  .icon{
    color: tomato;
}
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
  color: white;
  font-size: 40px;
  transition: all 0.3s ease-in;
}
.zf{
    transition: all 0.3s ease-in;
    font-size: 40px;
}
.zf:hover{
    color: yellow;
}
@keyframes text-blur{
    0%{
        color: transparent;
        transition-duration: 0s;
        text-shadow: tomato 0px 0px 15px;
        transform: scaleX(2.5);
        opacity: 0;
    }
    70%{
        color: transparent;
        text-shadow: tomato 0px 0px 14px;
        transform: scaleX(1.5);
        opacity: 0.5;
    }
    100%{
        color: transparent;
        text-shadow: tomato 0px 0px 0px;
        transform: scaleX(1);
        opacity: 1;
    }
}
</style>


