        document.querySelector('.top').style.height = height +'px';
        var video = document.querySelectorAll('.mask')[0];
    
        //滚动监听
        window.onscroll = function() {
        if(that.$route.path != '/pages/prevent'){
            return;
        }
        var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        var s1h =  document.querySelector('.s1').offsetHeight;
        var s2h =  document.querySelector('.s2').offsetHeight;
        var s3h =  document.querySelector('.s3').offsetHeight;
        var s4h =  document.querySelector('.s4').offsetHeight;
        var s3h4 =  document.querySelector('.s3-s4').offsetHeight;
        var h3 = document.querySelectorAll('h3');
        if(scrollTop>s1h/2){
            document.querySelector('.answer').classList.add('fadeInLeft');
            document.querySelector('.translation').style.animationDelay = '1s';
            document.querySelector('.translation').classList.add('fadeInLeft');
            document.querySelector('.s1 img').classList.add('fadeInRight');
            document.querySelector('.s1').classList.add('s1Slide');
            h3[1].classList.add('fadeInDown');
        }
        if(scrollTop>s1h + s2h/2){
            h3[2].classList.add('fadeInDown');
            var transmission = document.querySelectorAll('.s2 .transmission > div');
            for(let i =0;i<transmission.length;i++){
                transmission[i].style.animationDelay = i/3 + 's';
                transmission[i].classList.add('fadeInUp');
                transmission[i].classList.add('animated');
            }
        }
        if(scrollTop>s1h + s2h + s3h/2 ){
            var symptoms = document.querySelectorAll('.symptoms-child');
            var ys = document.querySelectorAll('.symptoms img');
            ys[0].classList.add('fadeInLeft');
            ys[0].classList.add('animated');
            ys[1].classList.add('fadeInRight');
            ys[1].classList.add('animated');
            for(let i=0;i<symptoms.length;i++){
                if(i>=3){
                    symptoms[i].style.animationDelay = i/3 + 's';
                    symptoms[i].classList.add('rollInRight');
                    symptoms[i].classList.add('animated');
                }else{
                    symptoms[i].style.animationDelay = i/3 + 's';
                    symptoms[i].classList.add('fadeInLeft');
                    symptoms[i].classList.add('animated');
                }
            }
            h3[3].classList.add('fadeInDown');
            h3[3].classList.add('animated');
        }
        if(scrollTop>s1h + s2h + s3h + s3h4/2){
            document.querySelector('.txzy-logo').classList.add('animated');
            document.querySelector('.txzy-logo').classList.add('bounceIn');
            document.querySelector('.txzy-btn').classList.add('animated');
            document.querySelector('.txzy-btn').style.animationDuration = '3s';
            document.querySelector('.txzy-btn').classList.add('fadeIn');
        }
        if(scrollTop>s1h + s2h + s3h + s3h4 +s4h/2){
            document.querySelector('.prevention>div:nth-of-type(2)').classList.add('animated');
            document.querySelector('.prevention>div:nth-of-type(2)').classList.add('fadeInRight');
            document.querySelector('.step').style.opacity = '1';
            var step = document.querySelectorAll('.step>*');
            for(let i=0;i<step.length;i++){
                step[i].style.animationDelay = i/3 + 's';
                step[i].classList.add('animated');
                step[i].classList.add('fadeInRight');
            }
            h3[4].classList.add('fadeInUp');
            h3[4].classList.add('animated');
        }

    }
    },
    methods:{
        play1(event){
           if(!this.playFlag){
                document.querySelector('.cover-play').classList.add('el-icon-video-pause');
                document.querySelector('.cover-play').classList.remove('el-icon-video-play');
                event.currentTarget.play();
                this.playFlag = !this.playFlag;
                document.querySelector('.cover').style.opacity = '0';
           }else{
                document.querySelector('.cover-play').classList.add('el-icon-video-play');
                document.querySelector('.cover-play').classList.remove('el-icon-video-pause');
               event.currentTarget.pause();
               this.playFlag  = !this.playFlag;
               document.querySelector('.cover').style.opacity = '1';
           }
        },
        play2(event){
           if(!this.play2Flag){
                document.querySelectorAll('.cover-play')[1].classList.add('el-icon-video-pause');
                document.querySelectorAll('.cover-play')[1].classList.remove('el-icon-video-play');
