                document.querySelectorAll('.cover')[1].style.opacity = '0';
           }else{
                document.querySelectorAll('.cover-play')[1].classList.add('el-icon-video-play');
                document.querySelectorAll('.cover-play')[1].classList.remove('el-icon-video-pause');
                event.currentTarget.pause();
                this.play2Flag  = !this.play2Flag;
                document.querySelectorAll('.cover')[1].style.opacity = '1';
           }
        }
    }
}
</script>

