    bottom: 0;
    z-index: 9999;
}
.tooth3{
    background-image: url(../assets/tooth3.png);
    height: 10px;
    width: 100% !important;
    margin: 0 !important;
}
.symptoms{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100% !important;
    margin: 0 !important;
    align-items: top;
}
.symptoms img{
    vertical-align: bottom;
    margin-right: 10px;
    margin-top: 100px
}
.symptoms-child{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 50px;
}
.symptoms-name{
    text-align: left;
}
.symptoms-name p{
    margin: 5px;
    margin-left: 0;
    margin-top: 0;
    font-weight: bold;
    font-size: 18px;
    text-align: left;
}
.symptoms-name small{
     font-size: 18px;
     color:#666;
}
.s3-s4{
    height:300px;
    background-color:#61D2B4 !important;
    position: relative;
}
.txzy-logo img{
    position: absolute;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 100px;
}
.txzy-btn button{
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    border: none;
    padding: 30px;
    padding-left: 60px;
    padding-right: 60px;
    border-radius: 10rem;
    color: tomato;
    background: white;
    box-shadow: 0 0 0px 6px #ffffff66;
    outline: none;
}
.pattern>div:nth-of-type(1){
    position: absolute;
    top: 45%;
    width: 80%;
    left: 10%;
    transform:translateY(-50%);
}
</style>


