<template>
    <div class="loading-div">
        <!-- 图片预加载 -->
        <div class="loading">
        <div class="left"></div>
        <div class="right"></div>
        <!-- 组件预加载 -->
        <Deeds v-if="flag" style="opacity:0"></Deeds>
        <Wish v-if="flag" style="opacity:0"></Wish>
        <Prevent v-if="flag" style="opacity:0"></Prevent>
    </div>
    <img src="../assets/loading.gif" alt="" class="loading-img animated" width="300px">
    </div>
</template>

<script>
import Deeds from '../pages/deeds';
import Wish from '../pages/wish';
import Prevent from '../pages/prevent';
export default {
    components:{Deeds,Wish,Prevent},
    data(){
        return{
            count:0,
            flag:true
        }
    },
    mounted(){
        this.preload();
        // setTimeout(() => {
        //         this.$emit('finished',{flag:true});
        //         document.querySelector('.loading-img').classList.add('fadeOut');
        //         document.querySelector('.left').classList.add('sideLeft');
        //         document.querySelector('.right').classList.add('sideRight');
        //         document.querySelector('.loading').classList.add('loadingNone');
        // }, 1000);
    },
    methods:{
        preload(){
        setTimeout(() => {
            this.flag = false;
            this.$emit('finished',{flag:true});
            document.querySelector('.loading-img').classList.add('fadeOut');
            document.querySelector('.left').classList.add('sideLeft');
            document.querySelector('.right').classList.add('sideRight');
            document.querySelector('.loading').classList.add('loadingNone');
        }, 3000);
        
    }
}
}
</script>

<style scoped>
.loading-div{
    position: fixed;
    z-index: 999;
    width: 100%;
    height: 100%;
    animation: loadingShow 1s both;
    opacity: 0;
    z-index: 9999;
}
@keyframes loadingShow{
    0%{
        opacity: 0;
    }
    50%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
.loading{
    position: fixed;
    z-index: 9999;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    font-family: font1;
    font-family: font2;
    font-family: font3;
}
.left{
    position: fixed;
    z-index: 9999;
    width: 50%;
    height: 100%;
    left: 0;
    top: 0;    
    background: url('../assets/loading-left.jpg');
    filter: brightness(.4) grayscale(50%);
    background-size: 100% 100%;
    /* filter: brightness(.7);
    background: linear-gradient(150deg, #59ffe6, #789eff); */
}
.right{
    position: fixed;
    z-index: 9999;
    width: 50%;
    height: 100%;
    left: 50%;
    top: 0;
    background: url('../assets/loading-right.jpg');
    filter:brightness(.4) grayscale(50%);
    background-size: 100% 100%;
    /* filter: brightness(.7);
    background: linear-gradient(-150deg, #59ffe6, #789eff); */
}
.loading-img{
    position: absolute;
    z-index: 9999;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
}
.sideLeft{
    animation: sideLeft 1s both;
}
@keyframes sideLeft {
    0%{
        transform: translateX(0);
}
    100%{
        transform: translateX(-100%);
        display: none;
    }
}
.sideRight{
    animation: Right 1s both;
}
@keyframes Right {
    0%{
        transform: translateX(0);
}
    100%{
        transform: translateX(100%);
        display: none;
    }
}


</style>


