<template>
    <div>
        <div class="index">
        <div class="nav-mask">
          <NavMenu v-on:show="change"></NavMenu>
        </div>
        <img  src="../assets/push.png" alt="" class="push" @click="push" >
        <div class="gz"></div>
        </div>
        <router-view/>
    </div>
</template>

<script>
import NavMenu from "../components/nav";
  export default {
    components:{NavMenu},
    data() {
      return {
        activeIndex: '1',
        activeIndex2: '1',
        flag:true,
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      push(event){
          if(this.flag){
            $('.nav-mask').css('display','block');
            this.flag = !this.flag;
            $(event.currentTarget).css('top','80px');
            $('.gz').css('height','130px');
            $(event.currentTarget).addClass('rotate');
          }else{
             $(event.currentTarget).removeClass('rotate');
                 $('.nav-mask').css('display','none');
            this.flag = !this.flag;
             $(event.currentTarget).css('top','0px');
            $('.gz').css('height','50px');
          }
      },
      change(msg){
        console.log(msg);
        this.flag = !this.flag;
      }
    },
  }
</script>

<style scoped>
@keyframes rotate {
  0%{
    transform: rotate(0deg) translateX(50%) translateY(50%);
  }
  25%{
    transform: rotate(30deg) translateX(50%) translateY(50%);
  }
  50%{
    transform: rotate(0deg) translateX(50%) translateY(50%);
  }
  75%{
    transform: rotate(-30deg) translateX(50%) translateY(50%);
  }
  100%{
    transform: rotate(0deg)  translateX(50%) translateY(50%);
  }
}
.rotate{
  transform-origin: 100% 100%;
  animation: 3s rotate 1s infinite
}
.icon {
  width: 100px;
  height: 100px;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
  position: fixed;
  top:0;
  right: 0;
  color:white;

}
.el-menu--horizontal>.el-menu-item{
    float: right;
    margin-right: 10px;
    background: none !important;
}
.el-menu--horizontal>.el-menu-item:nth-last-of-type(1){
    margin-left: 10px;
}
.index{
    position: fixed;
    right: 0;
    top: -60px;
    transition: top 0.3s ease-in-out;
    z-index: 999;
}
.push{
    position: fixed;
    height: 50px;
    top: 0px;
    right: 5%;
    transform: translateX(50%) translateY(50%);
    z-index: 99;
    transition: all 0.5s ease-in-out;
}
.gz{
  position: fixed;
  height: 50px;
  top: 0;
  width: 5px;
  background: rgb(255, 255, 255);
  right: 5%;
  transform: translateX(50%);
  transition: all 0.5s ease-in-out;
}
.el-menu.el-menu--horizontal{
  position: relative;
  border-bottom-left-radius: 10px;
  border: none;
  background: rgba(255, 166, 0, 0.5) !important;
}
.nav-mask{
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(15, 15, 15, 0.747);
}

.nav-mask .nav{
  position: fixed;
  left: 30%;
  top:35%;
  margin: 0;
}
</style>


