<template>
<div class="dataBG" style="position:fixed;width:100%;height:100%">
    <div class="back">
        <button>返回</button>
    </div>
    <div id="main"></div>
    <slideData :datas="msg" :res="res"></slideData>
    <small class="copyright">数据来源于第三方网络爬虫，仅供参考。</small>
</div>
</template>

<script>
import slideData from "../components/data";
export default {
    components:{slideData},
    data(){
        return{
           msg:'国内疫情数据',
           res:{},
           currentData:{},
           jwsr:[],
        }
    },
    methods:{
        updateProviceDate: function(res) { //更新各省份数据
        },
        updateCityDate: function(currentMapName, res) { //更新各市级视图
        },
        upadate(echarts, myChart, geoJson, option, data, geoCoordMap, currentMapName, convertData, provinceArray, flag) {
            if (currentMapName == '台湾省') {
                geoCoordMap[geoJson.features[0].properties.name] = [121.509062, 25.044332];
                data.push({
                    name: geoJson.features[0].properties.name,
                    //现存确诊数据接口
                    value: 10 * Math.ceil(10 * Math.random())
                });
            } else {
                for (let i = 0; i < geoJson.features.length; i++) {
                    var value;
                    geoCoordMap[geoJson.features[i].properties.name] = geoJson.features[i].properties.center;
                    if (flag) {
                        for (let j = 0; j < provinceArray.length; j++) {
                            if (provinceArray[j].childStatistic.substring(0, 3) == geoJson.features[i].properties.name.substring(0, 3) || provinceArray[j].childStatistic.substring(2, 4) == geoJson.features[i].properties.name.substring(0, 2)) {
                                value = provinceArray[j].currentConfirm;
                            }
                        }
                    } else {
                        for (let j = 0; j < provinceArray.length; j++) {
                            if (provinceArray[j].childStatistic.substring(0, 3) == currentMapName.substring(0, 3) || provinceArray[j].childStatistic.substring(2, 4) == geoJson.features[i].properties.name.substring(0, 2)) {
                                for (let k = 0; k < provinceArray[j].cityArray.length; k++) {
                                    if (provinceArray[j].cityArray[k].childStatistic.substring(0, 2) == geoJson.features[i].properties.name.substring(0, 2)) {
                                        value = provinceArray[j].cityArray[k].currentConfirm;
                                    }
                                }
                            }
                        }
                    }
                    data.push({
                        name: geoJson.features[i].properties.name,
                        //现存确诊数据接口
                        value: value
                    })
                }
            }
            console.log(geoCoordMap);
            echarts.registerMap(currentMapName, geoJson);
            //更新地图轮廓及边界数据
            option.series[0].data = convertData(data);
            option.series[1].data = data;
            option.series[2].data = convertData(data);
            option.series[3].data = convertData(data.sort(function(a, b) {
                return b.value - a.value;
            }).slice(0, 5));
            myChart.setOption(option);
        }
    },
    mounted(){
        var that = this;
        var staticData = {
        provinceGeo: {
        '上海市': 'https://geo.datav.aliyun.com/areas/bound/310000_full.json',
        '云南省': 'https://geo.datav.aliyun.com/areas/bound/530000_full.json',
        '内蒙古自治区': 'https://geo.datav.aliyun.com/areas/bound/150000_full.json',
        '北京市': 'https://geo.datav.aliyun.com/areas/bound/110000_full.json',
        '台湾省': 'https://geo.datav.aliyun.com/areas/bound/710000_full.json',
        '吉林省': 'https://geo.datav.aliyun.com/areas/bound/220000_full.json',
        '四川省': 'https://geo.datav.aliyun.com/areas/bound/510000_full.json',
        '天津市': 'https://geo.datav.aliyun.com/areas/bound/120000_full.json',
        '宁夏回族自治区': 'https://geo.datav.aliyun.com/areas/bound/640000_full.json',
        '安徽省': 'https://geo.datav.aliyun.com/areas/bound/340000_full.json',
        '山东省': 'https://geo.datav.aliyun.com/areas/bound/370000_full.json',
        '山西省': 'https://geo.datav.aliyun.com/areas/bound/140000_full.json',
        '广东省': 'https://geo.datav.aliyun.com/areas/bound/440000_full.json',
        '广西壮族自治区': 'https://geo.datav.aliyun.com/areas/bound/450000_full.json',
        '新疆维吾尔自治区': 'https://geo.datav.aliyun.com/areas/bound/650000_full.json',
        '江苏省': 'https://geo.datav.aliyun.com/areas/bound/320000_full.json',
        '江西省': 'https://geo.datav.aliyun.com/areas/bound/360000_full.json',
        '河北省': 'https://geo.datav.aliyun.com/areas/bound/130000_full.json',
        '河南省': 'https://geo.datav.aliyun.com/areas/bound/410000_full.json',
        '浙江省': 'https://geo.datav.aliyun.com/areas/bound/330000_full.json',
        '海南省': 'https://geo.datav.aliyun.com/areas/bound/460000_full.json',
        '湖北省': 'https://geo.datav.aliyun.com/areas/bound/420000_full.json',
        '湖南省': 'https://geo.datav.aliyun.com/areas/bound/430000_full.json',
        '澳门特别行政区': 'https://geo.datav.aliyun.com/areas/bound/820000_full.json',
        '甘肃省': 'https://geo.datav.aliyun.com/areas/bound/620000_full.json',
        '福建省': 'https://geo.datav.aliyun.com/areas/bound/350000_full.json',
        '西藏自治区': 'https://geo.datav.aliyun.com/areas/bound/540000_full.json',
        '贵州省': 'https://geo.datav.aliyun.com/areas/bound/520000_full.json',
        '辽宁省': 'https://geo.datav.aliyun.com/areas/bound/210000_full.json',
        '重庆市': 'https://geo.datav.aliyun.com/areas/bound/500000_full.json',
        '陕西省': 'https://geo.datav.aliyun.com/areas/bound/610000_full.json',
        '青海省': 'https://geo.datav.aliyun.com/areas/bound/630000_full.json',
        '香港特别行政区': 'https://geo.datav.aliyun.com/areas/bound/810000_full.json',
        '黑龙江省': 'https://geo.datav.aliyun.com/areas/bound/230000_full.json',
    }
}
        var res = {};
        var provinceArray = []; //省份数据
        var promise = new Promise(function(res, rej) { //队列化
                $.getScript("https://cdn.mdeer.com/data/yqstaticdata.js?callback=callbackstaticdata&t=" + (+new Date),
                    function() {
                        res();
                    }
                );
            })
        
        promise.then(function() { //链式调用 防止异步~
            $.getJSON(uploadedDataURL, function(geoJson) {
                that.upadate(echarts, myChart, geoJson, option, data, geoCoordMap, currentMapName, convertData, provinceArray, flag);
                that.updateProviceDate(res);
                console.log(res);
            });
        })
        
        //实时数据接口
        window.callbackstaticdata = function(respone) {
            that.res = respone;
            that.currentData = respone;
            res = respone;
            provinceArray = respone.provinceArray;
        }

        /*获取地图数据*/
        //各省份地图json api
        var myChart = echarts.init(document.getElementById('main'));
        var flag = true; //二层点击标志
        var provinceGeo = staticData.provinceGeo;
        var currentMapName = 'china';
        var max = 480,
            min = 9; // todo 
        var maxSize4Pin = 100,
            minSize4Pin = 20;
        var uploadedDataURL = 'https://geo.datav.aliyun.com/areas/bound/100000_full.json';
        var geoCoordMap = {};
        var data = [];
        var convertData = function(data) {
            var res = [];
            for (var i = 0; i < data.length; i++) {
                var geoCoord = geoCoordMap[data[i].name];
                if (geoCoord) {
                    res.push({
                        name: data[i].name,
                        value: geoCoord.concat(data[i].value),
                    });
                }
            }
            return res;
        };
        //option设置
        var option = {
            tooltip: {
                trigger: 'item',
                formatter: function(params) {
                    if (typeof(params.value)[2] == "undefined") {
                        return params.name + '<br>现存确诊: ' + params.value;
                    } else {
                        return params.name + '<br>现存确诊: ' + params.value[2];
                    }
                }
            },

            //视觉映射组件
            visualMap: {
                min: 0,
                max: 70,
                show: true,
                min: 0,
                color: 'white',
                left: '22%',
                bottom: '15',
                text: ['高', '低'], // 文本，默认为数值文本
                calculable: true,
                textStyle: {
                    color: "white"
                },
                seriesIndex: [1],
                inRange: {
                    color: ['yellow', 'red']
                }
            },

            //地图组件
            geo: {
                show: true,
                // --- //
                map: currentMapName,
                layoutCenter: ['50%', '65%'],
                // 如果宽高比大于 1 则宽度为 100，如果小于 1 则高度为 100，保证了不超过 100x100 的区域
                layoutSize: '120%',
                label: {
                    normal: {
                        show: false
                    },
                    emphasis: {
                        show: false,
                    }
                },
                itemStyle: {
                    normal: {
                        // areaColor: '#031525',
                        borderColor: '#ddd',
                        // shadowColor: 'orange', //外发光
                        // shadowBlur: 8
                    },
                    emphasis: {
                        areaColor: '#2B91B7',
                    }
                }
            },

            //序列
            series: [{
                    name: '散点',
                    type: 'scatter',
                    coordinateSystem: 'geo',
                    data: convertData(data),
                    symbolSize: function(val) {
                        return 5;
                    },
                    label: {
                        normal: {
                            formatter: '{b}',
                            align: 'center',
                            verticalAlign: 'top',
                            show: true,
                            color: '#fc6565'
                        },
                        emphasis: {
                            // show: true
                        }
                    },
                    itemStyle: {
                        normal: {
                            color: 'turquoise'
                        }
                    }
                }, {
                    type: 'map',
                    map: currentMapName,
                    geoIndex: 0,
                    animation: false,
                    layoutCenter: ['30%', '30%'],
                    // 如果宽高比大于 1 则宽度为 100，如果小于 1 则高度为 100，保证了不超过 100x100 的区域
                    layoutSize: 10,
                    data: data
                }, {
                    name: '点',
                    type: 'scatter',
                    coordinateSystem: 'geo',
                    symbol: 'pin', //气泡

                    symbolSize: function(val) {
                        if (val[2] == 0) {
                            return 0;
                        }
                        return 40;
                    },
                    label: {
                        normal: {
                            show: true,
                            textStyle: {
                                color: '#fff',
                                fontSize: 9,
                            },
                            formatter(value) {
                                return value.data.value[2]
                            }
                        }
                    },
                    itemStyle: {
                        normal: {
                            color: 'turquoise', //标志颜色
                        }
                    },
                    zlevel: 6,
                    data: convertData(data)
                }, {
                    name: 'Top 5',
                    type: 'effectScatter',
                    coordinateSystem: 'geo',
                    data: convertData(data.sort(function(a, b) {
                        return b.value - a.value;
                    }).slice(0, 5)),
                    symbolSize: function(val) {
                        if (val[2] == 0) {
                            return 0;
                        }
                        return 15;
                    },
                    showEffectOn: 'render',
                    rippleEffect: {
                        brushType: 'stroke'
                    },
                    hoverAnimation: true,
                    label: {
                        normal: {
                            formatter: '{b}',
                            align: 'center',
                            verticalAlign: 'top',
                            show: true,
                            color: 'red'
                        }
                    },
                    itemStyle: {
                        normal: {
                            color: 'turquoise',
                            fontSize: 20
                        }
                    },
                    zlevel: 1
                }

            ]
        };

        myChart.on('click', function(params) {
            var geoName = params.data.name.substring(0, 2);
            if (geoName == '香港' || geoName == '台湾' || geoName == '澳门') {
                return;
            }
            if (flag) { //判断是否为二级地图
                flag = !flag;
                data = [];
                geoCoordMap = {};
                //根据params获取到对应的urlchild子图geojson数据
                var urlChild = provinceGeo[params.data.name];
                $.getJSON(urlChild, function(geoJson) {
                    option.geo.layoutSize = '80%';
                    option.geo.layoutCenter = ['50%', '50%'];
                    currentMapName = params.data.name;
                    that.updateCityDate(currentMapName, res); //更新数据
                    echarts.registerMap(currentMapName, geoJson);
                    option.geo.map = currentMapName;
                    option.geo.zoom = 1;
                    option.series[1] = option.series[1];
                    that.upadate(echarts, myChart, geoJson, option, data, geoCoordMap, currentMapName, convertData, provinceArray, flag); //更新地图
                })
            }
        })

        //返回上一级
        document.getElementsByClassName('back')[0].addEventListener('click', function() {
            if (!flag) {
                flag = !flag;
                data = [];
                geoCoordMap = {};
                $.getJSON(uploadedDataURL, function(geoJson) {
                    option.geo.layoutSize = '120%';
                    option.geo.layoutCenter = ['50%', '65%'];
                    $('.datalist-name').html('中华人民共和国');
                    that.updateProviceDate(res);
                    that.upadate(echarts, myChart, geoJson, option, data, geoCoordMap, currentMapName, convertData, provinceArray, flag);
                });
            }
        })
            }
        }
</script>
<style scoped>
*{
    margin: 0;
    padding: 0;
    overflow: hidden;
}
.dataBG{
      background-image: linear-gradient(to right,rgb(68, 96, 156),rgb(23, 47, 97))
}
button{
    text-align: center
}
  #main{
      position: fixed;
      height: 98%;
      width: 100%;
      left: 0;
  }
  .back {
      position: absolute;
      left: 50%;
      z-index: 99;
      bottom: 20px;
      transform: translateX(-50%);
  }
  
  .back button:hover {
      color:#333;
  }
  
  .back button {
      background: rgb(238, 238, 238);
      border: none;
      color: #888;
      font-weight: 1000;
      font-size: 14px;
      background-size: 100% 100%;
      width: 100px;
      height: 40px;
      outline: none;
      border-radius: 10px;
      border: 2px solid rgb(49, 108, 218);
  }
    
  .top {
      position: absolute;
      top: 0;
      z-index: 99;
  }
  
  .top>img {
      width: 60%;
      margin-left: 20%;
  }
  
  .top p {
      position: absolute;
      top: 45%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      cursor: pointer;
      color: white;
      margin: 0;
      font-weight: 1000;
  }
  .copyright{
      position: fixed;
      bottom: 20px;
      right: 20px;
      color: #aaa;
      font-size: 13px;
  }
</style>



