import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import NavMenu from '@/components/nav'
import Wish from '@/pages/wish'
import Prevent from '@/pages/prevent'
import Main from '@/pages/main'
import FluData from '@/pages/fluData'
import Deeds from '@/pages/deeds'
import Loading from '@/components/loading'
import IntIncTrend from '@/pages/intIncTrend'


Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        component: Main
    }, {
        path: '/pages',
        name: 'Index',
        component: Index,
        children: [{
            path: 'wish',
            name: 'Wish',
            component: Wish
        }, {
            path: 'prevent',
            name: 'Prevent',
            component: Prevent
        }, {
            path: 'intIncTrend',
            name: 'IntIncTrend',
            component: IntIncTrend,
        }, {
            path: 'deeds',
            name: 'Deeds',
            component: Deeds
        }, {
            path: 'fluData',
            name: 'FluData',
            component: FluData
        }]
    }, {
        path: '/nav',
        component: NavMenu
    }, {
        path: '/loading',
        name: 'Loading',
        component: Loading
    }]
})


