<template>
  <div id="app">
    <router-view  v-if="loadingFlag"/>
    <Loading v-on:finished="finished" v-if="LoadingShow"></Loading>
  </div>
</template>

<script>
import Loading from './components/loading';
export default {
  name: 'App',
  data(){
    return{
      loadingFlag:false,
      LoadingShow:true
    }
  },
  mounted(){
    console.log(this.$route.fullPath)
  },
  methods:{
    finished(value){
        this.loadingFlag = value.flag;
        setTimeout(() => {
          this.LoadingShow = !this.LoadingShow;
        }, 1000);
    },
  },
  components:{Loading}
}
</script>

<style>
@import './common/font.css';
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body{
  margin: 0;
}
</style>



