<template>
    <div class="home">
        <div class="copyright">
            <span>© 2020 - now</span>
            <br>
            <span>版本归属-抗疫小分队</span>
        </div>
        <div class="home-page">
            <section class="cr-container">				
           
            <input id="select-img-1" name="radio-set-1" type="radio" class="cr-selector-img-1" />
            <label for="select-img-1" class="cr-label-img-1" style="display:none">1</label>
            
            <input id="select-img-2" name="radio-set-1" type="radio" class="cr-selector-img-2" />
            <label for="select-img-2" class="cr-label-img-2" style="display:none">2</label>
        
            <input id="select-img-3" name="radio-set-1" type="radio" class="cr-selector-img-3" />
            <label for="select-img-3" class="cr-label-img-3" style="display:none">3</label>

            <input id="select-img-4" name="radio-set-1" type="radio" class="cr-selector-img-4" />
            <label for="select-img-4" class="cr-label-img-4" style="display:none">4</label>
            
            <div class="clr"></div>	
            <div class="cr-bgimg" style="filter:brightness(.5) blur(5px)">
                <div>
                    <span>片1 - 图片1</span>
                    <span>片1 - 图片2</span>
                    <span>片1 - 图片3</span>
                    <span>片1 - 图片4</span>
                </div>
                <div>
                    <span>片2 - 图片1</span>
                    <span>片2 - 图片2</span>
                    <span>片2 - 图片3</span>
                    <span>片2 - 图片4</span>
                </div>
                <div>
                    <span>片3 - 图片1</span>
                    <span>片3 - 图片2</span>
                    <span>片3 - 图片3</span>
                    <span>片3 - 图片4</span>
                </div>
                <div>
                    <span>片4 - 图片1</span>
                    <span>片4 - 图片2</span>
                    <span>片4 - 图片3</span>
                    <span>片4 - 图片4</span>
                </div>
            </div>
        </section>
        </div>
        <div class="top animated bounceInLeft">
            <h2>欢迎来到新冠肺炎疫情专题网站</h2>
            <h3>- Welcome to the COVID-19 special website -</h3>
        </div>
        <div class="bottom animated bounceInRight">
            <h2>请选择你最想进入的篇章</h2>
            <h3>- Please select the chapter you want to enter most -</h3>
        </div>
        <NavMenu></NavMenu>
    </div>
</template>

<script>
import NavMenu from '../components/nav'
export default {
    components:{NavMenu},
    created(){
        window.scrollTo(0,0);
    },
    mounted(){   
        var section = document.querySelectorAll('.nav section');
        for(let i=0;i<section.length;i++){
            section[i].addEventListener('mouseenter',function(e){
                var index = e.currentTarget.getAttribute('index');
                var bg1 = document.querySelectorAll('.cr-container input ~ .cr-bgimg div span:nth-child('+ index +')');
                for(let i=0;i<bg1.length;i++){
                    bg1[i].style.transition = 'transform 0.5s ease-in-out, opacity 0.5s ease-in-out, z-index 0s linear 0.6s';
                    bg1[i].style.animation = 'none';
                    bg1[i].style.opacity = '1';
                    bg1[i].style.transform = 'scale(1.01)';
                    bg1[i].style.zIndex = '1000';
                    bg1[i].style.backgroundSize = '400% 100%';
                }
            })
        }
        for(let i=0;i<section.length;i++){
            section[i].addEventListener('mouseleave',function(e){
                var index = e.currentTarget.getAttribute('index');
                var bg1 = document.querySelectorAll('.cr-container input ~ .cr-bgimg div span:nth-child('+ index +')');
                for(let i=0;i<bg1.length;i++){
                    bg1[i].style.opacity = '0';
                   bg1[i].style.transition = 'transform 0.5s ease-in-out, opacity 0.5s ease-in-out, z-index 0s linear 0.6s';
                    bg1[i].style.animation = 'none';
                    bg1[i].style.transform = 'scale(1.5)';
                    bg1[i].style.zIndex = '1000';
                    bg1[i].style.backgroundSize = '400% 100%';
                }
            })
        }
    }
}
</script>

<style scoped>
.copyright{
    font-size: 15px;
    position: fixed;
    width: 100%;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 99;
    color: #ccc;
    cursor: pointer;
}
.copyright span:nth-of-type(1){
    font-family: font1;
}
.copyright span:nth-of-type(2){
    font-family: font2;
}
.home-page{
    position: fixed;
    width: 100%;
    height: 100%;
    background: linear-gradient(-150deg, #59ffe6, #789eff);
    background-size: 100% 100%;
    filter: brightness(.7);
    transition: all 0.3s ease-in;
}
.nav{
    border:2px solid white;
    /* box-shadow: 0 0 2px 2px #d1d1d1; */
}
.top{
    position: fixed;
    top:10%;
    text-align: center;
    width: 90%;
    margin-left: 5%;
    color: #fff;
}
h2{
    font-size: 24px;
    margin: 6px;
    font-weight: 500;
    font-family: font2;
}
h3{
    margin: 6px;
    font-weight: 500;
    font-family: font1;
}
.bottom{
    position: fixed;
    bottom:10%;
    text-align: center;
    width: 90%;
    margin-left: 5%;
    color: #fff;
}   
/* 背景变化特效 */
.cr-container{
	width: 100%;
	height: 100%;
	position: fixed;
	margin: 0 auto;
	overflow: hidden;
	/* border: 20px solid #fff;
	box-shadow: 1px 1px 3px rgba(0,0,0,0.1); */
}
.cr-container label{
	font-style: italic;
	width: 150px;
	height: 30px;
	cursor: pointer;
	color: #fff;
	line-height: 32px;
	font-size: 24px;
	float:left;
	position: relative;
	margin-top:350px;
	z-index: 1000;
}
.cr-container label:before{
	content:'';
	width: 34px;
	height: 34px;
	background: rgba(130,195,217,0.9);
	position: absolute;
	left: 50%;
	margin-left: -17px;
	border-radius: 50%;
	box-shadow: 0px 0px 0px 4px rgba(255,255,255,0.3);
	z-index:-1;
}
.cr-container label:after{
	width: 1px;
	height: 400px;
	content: '';
	background: linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
	position: absolute;
	bottom: -20px;
	right: 0px;
}
.cr-container label.cr-label-img-4:after{
	width: 0px;
}

.cr-container input{
	display: none;
}
.cr-bgimg{
	width: 100%;
	height: 100%;
	position: absolute;
	left: 0px;
	top: 0px;
	z-index: 1;
}
.cr-bgimg{
	background-repeat: no-repeat;
	background-position: 0 0;
}
.cr-bgimg div{
	width: 25%;
	height: 100%;
	position: relative;
	float: left;
	overflow: hidden;
	background-repeat: no-repeat;
}
.cr-bgimg div span{
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0px;
	left: 0px;
	transform: scale(1.5);
	opacity: 0;
	z-index: 2;
	text-indent: -9000px;
}
.cr-bgimg div:nth-child(1) span{
	background-position: 0px 0px;
}
.cr-bgimg div:nth-child(2) span{
	background-position: 33.3% 0px;
}
.cr-bgimg div:nth-child(3) span{
	background-position: 66.6% 0px;
}
.cr-bgimg div:nth-child(4) span{
	background-position: 100% 0px;
}
@keyframes scaleOut{
	0%{ transform: scale(1); opacity: 1; }
	100%{ transform: scale(1.5); opacity: 0; }
}
.cr-container input.cr-selector-img-1:hover ~ .cr-bgimg div span:nth-child(1),
.cr-container input.cr-selector-img-2:hover ~ .cr-bgimg div span:nth-child(2),
.cr-container input.cr-selector-img-3:hover ~ .cr-bgimg div span:nth-child(3),
.cr-container input.cr-selector-img-4:hover ~ .cr-bgimg div span:nth-child(4)
{
	transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out, z-index 0s linear 0.6s;
	animation: none;
	opacity: 1;
	transform: scale(1);
	z-index:10000;
}
.cr-container input.cr-selector-img-1:hover ~ .cr-bgimg,
.cr-bgimg div span:nth-child(1){
	background-image: url(../assets/b1.jpg);
    background-size: 400% 100%;
}
.cr-container input.cr-selector-img-2:hover ~ .cr-bgimg,
.cr-bgimg div span:nth-child(2){
	background-image: url(../assets/b2.jpg);
    background-size: 400% 100%;
}
.cr-container input.cr-selector-img-3:hover ~ .cr-bgimg,
.cr-bgimg div span:nth-child(3){
	background-image: url(../assets/b3.jpg);
    background-size: 400% 100%;
}
.cr-container input.cr-selector-img-4:hover ~ .cr-bgimg,
.cr-bgimg div span:nth-child(4){
	background-image: url(../assets/b4.jpg);
    background-size: 400% 100%;
}
.cr-container input:hover ~ .cr-bgimg div span{
	animation: scaleOut 0.6s ease-in-out;
}
</style>


