<template>
    <div style="position:fixed;height:100%;width:100%" class="dataBG">
        <div id="main"></div>
        <slideData v-on:sendData="getData" :datas="msg"></slideData>
        <el-select v-model="value">
        <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
        </el-select>
        <small class="copyright">数据来源于第三方网络爬虫，仅供参考。</small>
    </div>
</template>
<style scoped>
.dataBG{
    background-image: linear-gradient(to right,rgb(68, 96, 156),rgb(23, 47, 97));
}
#main{
    position: fixed !important;
    height: 80%;
    width: 80%;
    left: 10%;
    top: 15% !important;
    overflow: hidden;
}
.el-select{
    position: fixed;
    top:20%;
    left: 50%;
    transform:translateX(-50%);
}
.b1{
    background: url('../assets/tf.png');
    background-size: 100% 100%;
    background-color: red;
}
.copyright{
      position: fixed;
      bottom: 20px;
      right: 20px;
      color: #aaa;
      font-size: 13px;
  }
</style>
<script>
import slideData from "../components/data";
export default {
    components:{slideData},
    methods:{
        getData(data){
            this.res = data;
            console.log(data);
        }
    },
    data(){
        return{
            msg:'全球疫情数据',
            options: [{
                value:'选项1',
                label:'国内疫情趋势'
            },{
                value:'选项2',
                label:'国外疫情趋势'
            },{
                value:'选项3',
                label:'美国疫情趋势'
            },{
                value:'选项4',
                label:'西班牙疫情趋势'
            },{
                value:'选项5',
                label:'俄罗斯疫情趋势'
            },],
            value:'国内疫情趋势',    
            res:{},
        }
    },
    mounted(){
        //初始化
        var that = this;
        var main = document.getElementById('main');
        var mychart3 = echarts.init(main);
        var timeData = [];
        var sureData = [];
        var deathData = [];
        var curedData = [];
        $('.data-input').html('死亡');
        $('.data-input').css('color','#303133');
        $('.datalist-name').css('display', 'none');
        $('.china-data').css('display', 'none');
        $('.el-divider--horizontal').css('display','none');
        $('.dataChild').css('margin-top','20px');
        // 国内新增趋势
        setTimeout(() => {
                for (let i = that.res.incTrend.length - 15; i < that.res.incTrend.length; i++) {
                timeData.push(that.res.incTrend[i].day);
                sureData.push(that.res.incTrend[i].sure_cnt);
                deathData.push(that.res.incTrend[i].die_cnt);
                curedData.push(that.res.incTrend[i].cure_cnt);
                }
                mychart3.hideLoading();
                mychart3.setOption(option)
        }, 1000);
        //国外疫情新增数据
        $('.el-select-dropdown__item').on('click',function(){
            var indexGID = $(this).index();
            timeData = [];
            sureData = [];
            deathData = [];
            curedData = [];
            var index = indexGID - 1;
            if (index == -1) {
                for (let i = that.res.incTrend.length - 15; i < that.res.incTrend.length; i++) {
                    timeData.push(that.res.incTrend[i].day);
                    sureData.push(that.res.incTrend[i].sure_cnt);
                    deathData.push(that.res.incTrend[i].die_cnt);
                    curedData.push(that.res.incTrend[i].cure_cnt);
                }
                option.xAxis[0].data = timeData;
                option.series[0].data = sureData;
                option.series[1].data = curedData;
                option.series[2].data = deathData;
                mychart3.setOption(option);
            } else {
                for (let i = that.res.intTrend[index].trend.length - 25; i < that.res.intTrend[index].trend.length; i++) {
                    timeData.push(that.res.intTrend[index].trend[i].day);
                    sureData.push(that.res.intTrend[index].trend[i].inc_sure);
                    deathData.push(that.res.intTrend[index].trend[i].die_cnt - that.res.intTrend[index].trend[i - 1].die_cnt);
                    curedData.push(that.res.intTrend[index].trend[i].cure_cnt - that.res.intTrend[index].trend[i - 1].cure_cnt);
                }
                option.xAxis[0].data = timeData;
                option.series[0].data = sureData;
                option.series[1].data = curedData;
                option.series[2].data = deathData;
                mychart3.setOption(option);
            }
        })
        var option = {  
            // backgroundColor:'red',
            legend: {
                show: true,
                x: 'center',
                top: '15%',
                y: '35',
                icon: 'stack',
                itemWidth: 24,
                itemHeight: 15,
                textStyle: {
                    color: '#1bb4f6'
                },
                data: ['新增确诊数', '新增治愈数', '新增死亡数']
            },
            tooltip: {
                show: true,
                // formatter: `时间:2020.{b}<br>{a1}:{c1}<br>{a0}:{c0}<br>{a2}:{c2}`,
                formatter:function(params){
                    var str = '';
                    for(let i=0;i<params.length;i++){
                      str = str + params[i].marker + params[i].seriesName + ' : ' + params[i].value + '人<br>';
                    }
                    return str;
                },
                textStyle:{
            　　    align:'left'
            　　},
                trigger: 'axis',
                axisPointer: {
                    lineStyle: {
                        color: {
                            type: 'linear',
                            x: 0,
                            y: 0,
                            x2: 0,
                            y2: 1,
                            colorStops: [{
                                offset: 0,
                                color: 'rgba(0, 255, 233,0)'
                            }, {
                                offset: 0.5,
                                color: 'rgba(255, 255, 255,1)',
                            }, {
                                offset: 1,
                                color: 'rgba(0, 255, 233,0)'
                            }],
                        }
                    },
                },
            },
            grid: {
                top: '25%',
                left: '25%',
                right: '25%',
                bottom: '20%',
            },
            xAxis: [{
                type: 'category',
                axisLine: {
                    show: true
                },
                splitArea: {
                    // show: true,
                    color: '#f00',
                    lineStyle: {
                        color: '#f00'
                    },
                },
                axisLabel: {
                    margin: 30,
                    color: '#fff'
                },
                splitLine: {
                    show: false
                },
                boundaryGap: false,
                data: timeData,
            }],

            yAxis: [{
                type: 'value',
                min: 0,
                max: sureData.concat(curedData).concat(deathData).sort()[0],
                splitNumber: 4,
                splitLine: {
                    show: true,
                    lineStyle: {
                        color: 'rgba(255,255,255,0.1)'
                    }
                },
                axisLine: {
                    show: true,
                },
                axisLabel: {
                    show: true,
                    margin: 15,
                    textStyle: {
                        color: '#d1e6eb',

                    },
                },
                axisTick: {
                    show: false,
                },
            }],
            series: [{
                    name: '新增确诊数',
                    type: 'line',
                    showAllSymbol: true,
                    symbol: 'circle',
                    symbolSize: 15,
                    lineStyle: {
                        normal: {
                            color: "#fc6565",
                            shadowColor: 'rgba(0, 0, 0, .3)',
                            shadowBlur: 0,
                            shadowOffsetY: 5,
                            shadowOffsetX: 5,
                        },
                    },
                    label: {
                        show: true,
                        position: 'top',
                        textStyle: {
                            color: '#fc6565',
                        }
                    },
                    itemStyle: {
                        color: "#fc6565",
                        borderColor: "#fff",
                        borderWidth: 2,
                        shadowColor: 'rgba(0, 0, 0, .3)',
                        shadowBlur: 0,
                        shadowOffsetY: 2,
                        shadowOffsetX: 2,
                    },
                    areaStyle: {
                        normal: {
                            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                    offset: 0,
                                    color: 'rgba(108,80,243,0.3)'
                                },
                                {
                                    offset: 1,
                                    color: 'rgba(108,80,243,0)'
                                }
                            ], false),
                            shadowColor: 'rgba(108,80,243, 0.9)',
                            shadowBlur: 20
                        }
                    },
                    data: sureData
                },
                {
                    name: '新增治愈数',
                    type: 'line',
                    showAllSymbol: true,
                    symbol: 'circle',
                    symbolSize: 15,
                    lineStyle: {
                        normal: {
                            color: "#00ca95",
                            shadowColor: 'rgba(0, 0, 0, .3)',
                            shadowBlur: 0,
                            shadowOffsetY: 5,
                            shadowOffsetX: 5,
                        },
                    },
                    label: {
                        show: true,
                        position: 'top',
                        textStyle: {
                            color: '#00ca95',
                        }
                    },

                    itemStyle: {
                        color: "#00ca95",
                        borderColor: "#fff",
                        borderWidth: 2,
                        shadowColor: 'rgba(0, 0, 0, .3)',
                        shadowBlur: 0,
                        shadowOffsetY: 2,
                        shadowOffsetX: 2,
                    },
                    areaStyle: {
                        normal: {
                            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                    offset: 0,
                                    color: 'rgba(0,202,149,0.3)'
                                },
                                {
                                    offset: 1,
                                    color: 'rgba(0,202,149,0)'
                                }
                            ], false),
                            shadowColor: 'rgba(0,202,149, 0.9)',
                            shadowBlur: 20
                        }
                    },
                    data: curedData,
                }, {
                    name: '新增死亡数',
                    type: 'line',
                    showAllSymbol: true,
                    symbol: 'circle',
                    symbolSize: 15,
                    lineStyle: {
                        normal: {
                            color: "#9e9e9e",
                            shadowColor: 'rgba(0, 0, 0, .3)',
                            shadowBlur: 0,
                            shadowOffsetY: 5,
                            shadowOffsetX: 5,
                        },
                    },
                    label: {
                        show: true,
                        position: 'top',
                        textStyle: {
                            color: '#9e9e9e',
                        }
                    },

                    itemStyle: {
                        color: "#9e9e9e",
                        borderColor: "#fff",
                        borderWidth: 2,
                        shadowColor: 'rgba(0, 0, 0, .3)',
                        shadowBlur: 0,
                        shadowOffsetY: 2,
                        shadowOffsetX: 2,
                    },
                    areaStyle: {
                        normal: {
                            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                    offset: 0,
                                    color: 'rgba(0,202,149,0.3)'
                                },
                                {
                                    offset: 1,
                                    color: 'rgba(0,202,149,0)'
                                }
                            ], false),
                            shadowColor: 'rgba(0,202,149, 0.9)',
                            shadowBlur: 20
                        }
                    },
                    data: deathData,
                }
            ]
        };
    },
}
</script>


