<template>
  <div class="wish">
    <el-carousel direction="vertical" :height="height" :autoplay="false" ref="carousel" :loop="false" @change="((next, pre) => {indexChange(next, pre)})">
        <section class="process animated" style="background:none">
            <div class="restart-div animated fadeIn">
                <div class="progress"></div>
                <canvas class="hearts-canvas"></canvas>
                <el-button type="primary" round class="restart">重启武汉</el-button>
            </div>
        </section>
        <el-carousel-item index="0">
            <section class="process animated fadeInDown"></section>
            <img src="../assets/p1.jpg" alt="" width="100%" height="100%" class="show">
            <img src="../assets/t1.png" alt="" class="t-odd animated" width="200px">
            <img src="../assets/t2.png" alt="" class="t-even animated" width="100px">
        </el-carousel-item>
        <el-carousel-item index="1">
            <section class="process animated"></section>
            <img src="../assets/p2.jpg" alt="" width="100%" height="100%" class="show">
            <img src="../assets/t3.png" alt="" class="t-odd animated" width="220px">
            <img src="../assets/t4.png" alt="" class="t-even animated" width="250x">
        </el-carousel-item>
        <el-carousel-item index="2">
            <section class="process animated"></section>
            <img src="../assets/p3.jpg" alt="" width="100%" height="100%" class="show">
            <img src="../assets/t5.png" alt="" class="t-odd animated" width="250px">
            <img src="../assets/t6.png" alt="" class="t-even animated" width="220px">
        </el-carousel-item>
        <el-carousel-item index="3">
            <section class="process animated"></section>
            <img src="../assets/p4.jpg" alt="" width="100%" height="100%" class="show">
            <img src="../assets/t7.png" alt="" class="t-odd animated" width="250px">
            <img src="../assets/t3.png" alt="" class="t-even animated" width="220px">
        </el-carousel-item>
        <el-carousel-item index="4">     
            <section class="process animated"></section> 
            <img src="../assets/p5.jpg" alt="" width="100%" height="100%" class="show">
            <img src="../assets/t1.png" alt="" class="t-odd animated" width="200px">
            <img src="../assets/t2.png" alt="" class="t-even animated" width="100px">
        </el-carousel-item>
    </el-carousel>
  </div>
</template>
<script>
export default {
    data() {
        return {
            activeIndex:0,
            percentage: 0,
            alreadyActive:[],
            height:'0px',
            flag:false, //上一张图是否已点亮
            cp:{},
            colors: [{
                color: '#f56c6c',
                percentage: 20
            }, {
                color: '#e6a23c',
                percentage: 40
            }, {
                color: '#5cb87a',
                percentage: 60
            }, {
                color: '#1989fa',
                percentage: 80
            }, {
                color: '#6f7ad3',
                percentage: 100
            }]
        }
    },
    mounted() {
        this.height = $(window).height() +'px';
        var that = this;
        this.heart();
        var MAX = 37;
        const cp = new CircleProgress('.progress', {
            max: 100,
            size: 10,
            value: 0,
            animationDuration: 300,
            textFormat: (val) => '进度&nbsp;' + val + '°',
        });
        this.cp = cp;
        var process = document.getElementsByClassName('process')[0];
        document.getElementsByClassName('restart')[0].addEventListener('click', function(event) {
            var active = document.querySelectorAll('.wish .is-active')[0];
            var index = active.getAttribute('index');
            if (cp.value < 80) {
                cp.value = cp.value + 20;
                cp.el.style.setProperty('--progress-value', cp.value / MAX);
                this.style.boxShadow = '0 0 4px 3px #ffb676';
                this.style.color = '#eee';
                setTimeout(() => {
                    this.style.boxShadow = '0 0 0px 0px #ffb676';
                    this.style.color = '#888';
                }, 200)
            } else if (cp.value >= 80) {
                that.flag = !that.flag;
                cp.value = cp.value + 20;
                var show = document.getElementsByClassName('show');
                var text1 = document.getElementsByClassName('t-odd')[index];
                var text2 = document.getElementsByClassName('t-even')[index];
                var activeProcess = document.querySelectorAll('.process')[parseInt(index)+1];
                that.alreadyActive.push(index);
                setTimeout(() => {
                    this.style.transitionDuration = '0.8s';
                    this.style.backgroundColor = '#16ca16';
                    this.style.boxShadow = '0 0 1120px 120px #ffca00d1';
                    this.style.color = '#fff';
                    this.innerHTML = '重启成功';
                    process.style.animationDelay ="1.5s";
                    process.style.animationDuration = "2.5s";
                    process.classList.add('fadeOut');
                    activeProcess.style.animationDelay ="1.5s";
                    activeProcess.style.animationDuration = "2.5s";
                    activeProcess.classList.add('fadeOut');
                    text1.classList.add('fadeInUp');
                    text2.classList.add('fadeInDown');
                    show[index].style.filter = 'grayscale(0%)';
                }, 200);
            }
        });
      
        window.addEventListener('mousewheel', this.debounce(this.handleScroll, 300), true) || window.addEventListener("DOMMouseScroll", this.debounce(this.handleScroll, 100), false);
    },
    methods: {
        increase() {
            this.percentage += 10;
            if (this.percentage > 100) {
                this.percentage = 100;
            }
        },
        //函数防抖
        debounce(func, wait) {
            let timeout;
            return function() {
                let context = this;
                let args = arguments;
                if (timeout) clearTimeout(timeout);
                timeout = setTimeout(() => {
                    func.apply(context, args)
                }, wait);
            }
        },
        handleScroll(event) {//滚轮触发轮播函数
            var e = window.event || event;
            var process =  document.getElementsByClassName('process')[0];
            if(document.querySelectorAll('.nav-mask')[0]){
                if(document.querySelectorAll('.nav-mask')[0].style.display == 'block'){
                return;
            }
            }
            if (e.deltaY > 0) {
                if(this.$refs.carousel){
                    this.$refs.carousel.next();
                }
            } else {
                if(this.$refs.carousel){
                    this.$refs.carousel.prev();
                }
            }
        },
        indexChange(next, pre){
            var processAll = document.querySelectorAll('.process');
            var restartBtn = document.querySelectorAll('.restart')[0];
            var already = false;
            processAll[next+1].classList.add('fadeInDown');
            this.cp.value = 0;
            restartBtn.style.color = '#888';
            restartBtn.style.transitionDuration = '0.2s';
            restartBtn.style.boxShadow = 'none';
            restartBtn.style.background = 'rgb(66, 43, 1)';
            restartBtn.innerHTML = '重启武汉';
            for(let i = 0;i<this.alreadyActive.length;i++){
                if(parseInt(this.alreadyActive[i]) == parseInt(next)){
                    already = true;
                }
            }
            if(already){
                processAll[0].style.animationDuration = '0.5s';
                processAll[0].style.animationDelay = '0s';
                processAll[0].classList.add('fadeOut');
            }else{
                processAll[0].classList.remove('fadeOut');
                processAll[0].classList.add('fadeIn');
            }
            
        },
        heart() { //爱心canvas构建函数
            class HeartsFlow {
                constructor(data) {
                    this.el = document.querySelector(data.canvasEl);
                    this.w = 400;
                    this.h = 600;
                    this.ctx = this.el.getContext('2d');
                    this.colors = [   //爱心颜色
                        '255, 137, 164', //'#FF89A4',
                        '239, 121, 138', //'#EF798A',
                        '255, 77, 128', //'#FF4D80',
                        '249, 42, 130' //'#F92A82'
                    ];
                    this.heartsAmount = data.amount;
                    this.heartsList = [];
                    this.isAnimate = false;
                    this.raf = null;
                    this.animate = this.animate.bind(this);
                    this.paintHeart = this.paintHeart.bind(this);
                    this.stopAnimation = this.stopAnimation.bind(this);
                    this.init();
                }
                getRandomColor() {
                    return this.colors[Math.floor(Math.random() * this.colors.length)];
                }
                getRandom(min, max) {
                    return Math.floor(Math.random() * (max - min) + min);
                }
                setHeartsList() {
                    let arr = [];
                    for (let i = 0; i < this.heartsAmount; i++) {
                        let currentSize = this.getRandom(10, 15);  //爱心大小设置
                        let dt = { 
                            x: this.w / 2,
                            y: this.h,
                            bx: this.w / 2,
                            by: this.h,
                            pos: this.h,
                            _osp: this.getRandom(200, 400) / 100,
                            osp: this.getRandom(11, 12) / 20,
                            vsp: this.getRandom(currentSize, currentSize + i * 2) / 1000,
                            size: currentSize,
                            color: this.getRandomColor(),
                            alfa: 1
                        };

                        arr.push(dt);
                    }
                    this.heartsList = [...this.heartsList, ...arr];
                }
                getCoordinates({
                    x,
                    y,
                    size,
                    color,
                    bx,
                    by,
                    _osp,
                    osp,
                    vsp,
                    pos,
                    alfa
                }) {
                    return {
                        xst: x,
                        yst: y + size / 2,
                        x0: x - size / 1.4,
                        y0: y + size / 4,
                        x1: x - size / 1.3,
                        y1: y - size / 1.3,
                        _x0: x + size / 1.4,
                        _y0: y + size / 4,
                        _x1: x + size / 1.3,
                        _y1: y - size / 1.3,
                        xfn: x,
                        yfn: y - size / 3,
                        bx: bx,
                        by: by,
                        _osp: _osp,
                        osp: osp,
                        vsp: vsp,
                        pos: pos,
                        alfa: alfa,
                        size: size,
                        color: color
                    };

                }
                paintHeart({
                    xst,
                    yst,
                    x0,
                    y0,
                    _x0,
                    _y0,
                    x1,
                    y1,
                    _x1,
                    _y1,
                    xfn,
                    yfn,
                    color,
                    alfa
                }) {
                    this.ctx.globalCompositeOperation = "lighter";
                    this.ctx.beginPath();
                    this.ctx.moveTo(xst, yst);
                    this.ctx.bezierCurveTo(x0, y0, x1, y1, xfn, yfn);
                    this.ctx.moveTo(xst, yst);
                    this.ctx.bezierCurveTo(_x0, _y0, _x1, _y1, xfn, yfn);
                    this.ctx.fillStyle = `rgba(${color}, ${alfa})`;
                    this.ctx.strokeStyle = `rgba(${color}, ${alfa})`;
                    this.ctx.fill();
                    this.ctx.stroke();
                    this.ctx.closePath();
                }
                mutateData() {
                    this.heartsList = this.heartsList.map(item => {
                        let pos = item.pos - 0.05;
                        //爱心行走路径
                        let x = item.x + Math.sin(pos * item._osp) * ((pos - item.by) / item.osp);
                        let y = pos + (pos - item.by) / item.vsp * 1.6;
                        let alfa = this.normalize0between1(0, this.h, y).toFixed(1);
                        return {
                            ...item,
                            x: x,
                            y: y,
                            pos: pos,
                            alfa: alfa
                        };
                    });
                    this.heartsList = this.heartsList.filter(item => item.y > 0);
                }
                normalize0between1(min, max, value) {
                    return (value - min) / (max - min);
                }
                setCanvas() {
                    this.el.width = this.w;
                    this.el.height = this.h;
                }
                startAnimation() {
                    if (!this.isAnimate) {
                        this.isAnimate = true;
                        console.log('start animation');
                        this.setHeartsList();
                        this.animate();
                    } else {
                        this.setHeartsList();
                    }
                }
                stopAnimation() {
                    this.isAnimate = false;
                    console.log('stop animation');
                    cancelAnimationFrame(this.raf);
                }
                animate() {
                    this.ctx.clearRect(0, 0, this.w, this.h);
                    if (this.isAnimate) {
                        for (let i = 0, len = this.heartsList.length; i < len; i++) {
                            let hrt = this.getCoordinates(this.heartsList[i]);
                            this.paintHeart(hrt);
                        }
                        this.mutateData();
                    }
                    this.raf = requestAnimationFrame(this.animate);
                    if (this.heartsList.length === 0 && this.isAnimate) {
                        this.stopAnimation();
                    }
                }
                init() {
                    this.setCanvas();
                    this.setHeartsList();
                    this.animate();
                }
            }
            let ht = new HeartsFlow({
                canvasEl: '.hearts-canvas',
                amount: 20
            });
            let btn = document.querySelector('.restart');
            var that = this;
            btn.addEventListener('click', function() {
                console.log(3)
                var active = document.querySelectorAll('.wish .is-active')[0];
                var index = active.getAttribute('index');
                for(let i = 0;i<that.alreadyActive.length;i++){
                    if(parseInt(that.alreadyActive[i]) == parseInt(index)){
                        return;
                    }
                }
                ht.startAnimation();
            });
        }
    },
}
</script>

<style scoped>
.wish {
    position: fixed;
    height: 100%;
    width: 100%;
}

.restart {
    position: fixed;
    top: 40%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    margin-top: 180px;
}

.restart-div{
    animation-duration: 1.5s;
}

.el-carousel__item {
    -webkit-transition: -webkit-transform .4s ease-in-out;
    transition: -webkit-transform .4s ease-in-out;
    transition: transform .4s ease-in-out;
    transition: transform .4s ease-in-out, -webkit-transform .4s ease-in-out;
    transition: transform .4s ease-in-out, -webkit-transform .4s ease-in-out;
}

.el-carousel,
.el-carousel__container {
    height: 100%;
}

.el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
}

.show {
    -webkit-filter: grayscale(100%) blur(4px);
    /* Chrome, Safari, Opera */
    filter: grayscale(100%) blur(4px);
    transition: filter 2s ease-in;
    transition-delay: 3s;
}

.process {
    background: url('../assets/bg3.png') no-repeat;
    background-size: 100% 100%;
    position: absolute;
    width: 26%;
    margin-left: 37%;
    height: 100%;
    z-index: 99;
}
.el-progress {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

.el-button.is-round {
    transition: all 0.2s ease-in;
    border: none;
    outline: none;
    font-weight: 1000;
    background: rgb(66, 43, 1);
    color: #888;
}

.t-odd {
    position: absolute;
    opacity: 0;
    bottom: 20%;
    left: 5%;
    animation-delay: 5.5s;
}

.t-even {
    position: absolute;
    opacity: 0;
    bottom: 28%;
    right: 10%;
    animation-delay: 5.5s;
}

/* 进度条 */
.progress {
    --progress-value: .3243;
    --color: hsl(calc(240 * (1 - var(--progress-value))), 100%, calc(30% + 20% * var(--progress-value)));
}

/deep/ .circle-progress {
    position: fixed;
    top: 40%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 180px;
    height: 180px;
}

/deep/ .circle-progress-value {
    stroke-width: 6px;
    stroke: var(--color);
    stroke-dasharray: 5.98 2;
    transition: stroke .4s;
}

/deep/ .circle-progress-circle {
    stroke-width: 2px;
    stroke: #17181c;
}

/deep/ .circle-progress-text {
    font-weight: bold;
    fill: var(--color);
    transform: translateX(0.2em);
    transition: fill .4s;
}

/deep/ input[type=range] {
    width: 210px;
    margin-top: 30px;
    margin-bottom: 20px;
    -webkit-appearance: none;
    display: inline-block;
    cursor: pointer;
}

/deep/ input[type=range]:focus {
    outline: none;
}

/deep/ input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 6px;
    background: #17181c;
    outline: none;
}

/deep/ input[type=range]::-moz-range-track {
    width: 100%;
    height: 6px;
    background: #17181c;
    outline: none;
}

/deep/ input[type=range]::-ms-track {
    width: 100%;
    height: 6px;
    background: #17181c;
    outline: none;
}

/deep/ input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 16px;
    height: 16px;
    margin-top: -5px;
    background: #464953;
    transition: .15s;
}

/deep/ input[type=range]::-moz-range-thumb {
    width: 16px;
    height: 16px;
    margin-top: -5px;
    background: #464953;
    transition: .15s;
}

/deep/ input[type=range]::-ms-thumb {
    width: 16px;
    height: 16px;
    margin-top: -5px;
    background: #464953;
    transition: .15s;
}

/deep/ input[type=range]:hover::-webkit-slider-thumb,
/deep/ input[type=range]:focus::-webkit-slider-thumb {
    background: #75798a;
}

/deep/ input[type=range]:hover::-moz-range-thumb,
/deep/ input[type=range]:focus::-moz-range-thumb {
    background: #75798a;
}

/deep/ input[type=range]:hover::-ms-thumb,
/deep/ input[type=range]:focus::-ms-thumb {
    background: #75798a;
}

/deep/ a {
    color: #75798a;
}


/* 爱心特效 */
.hearts-canvas{
    position: fixed;
    top: 40%;
    left: 50%;
    margin-top: 180px;
    transform: translateX(-50%) translateY(-100%);
}
.liker {
    position: absolute;
    top: 0%;
    left: 50%;
    transform: translate(-50%, 0%);
}

.liker span {
    position: absolute;
    font-family: monospace;
    color: #F92A82;
    bottom: -70px;
    left: 50%;
    transform: translateX(-50%);
}
</style>


